.main-nav {
  z-index: 3;
  min-height: 0;
  font-weight: 700;
  line-height: 32px;
}
.main-nav__toggle {
  position: absolute;
  top: 21px;
  right: 20px;
  z-index: 3;
  display: block;
  width: 50px;
  height: 24px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 43px;
  }
}
@media (min-width: 1070px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
@media (max-width: 1070px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 23px;
  right: 20px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 29px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__toggle {
    top: 43px;
  }
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav--opened .main-nav__wrapper::before {
  content: "";
  position: absolute;
  top: -70px;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 69px;
  background: #283645;
  box-shadow: 0 1px 0 #000000;
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__wrapper::before {
    top: -110px;
    height: 109px;
  }
}
@media (min-width: 1070px) {
  .main-nav--opened .main-nav__wrapper::before {
    content: none;
  }
}
